import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { map, mapTo, catchError } from 'rxjs/operators';
import { GlobalData, CountryWiseData, StateWise, timeSeries, news, districtDataOverall } from './data';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class GetDataService {

  userJson : GlobalData;

  constructor(private _http : HttpClient,private _route:Router) { }

  getJsonData() : Observable<any>{
    
   return this._http.get('https://api.covid19api.com/summary');

   
  }

  getCountryWiseStateData(){

   return this._http.get('https://api.covid19india.org/data.json');
   
 }

  

  getCountryWiseStateDataIndia(){

     return this._http.get('https://api.covid19india.org/data.json').pipe(
       map((data:StateWise[])=>{
         
         return data; 
       }), catchError( error => {
        return throwError( 'Something went wrong!' );
      })
     )
    
  }

  getCountryWiseDistrictDataIndia(){

    return this._http.get('https://api.covid19india.org/state_district_wise.json').pipe(
      map((data:districtDataOverall[])=>{
        return data; 
      }), catchError( error => {
       return throwError( 'Something went wrong!' );
      })
    );
   
 }

  getTimeLine(){

    return this._http.get('https://api.covid19india.org/data.json').pipe(
      map((data:timeSeries[])=>{
        
        return data; 
      }), catchError( error => {
       return throwError( 'Something went wrong!' );
     })
    )
   
 }

 getCoronaVirusNews(){
  
  return this._http.get('https://newsapi.org/v2/top-headlines?country=in&category=health&'+
  'pageSize=100&'+
  'apiKey=48a787e36e5d46a1a775fa69ff0e75ab').pipe(
    map((data:news[])=>{
      return data;
    }), catchError( error => {
      return throwError( 'Something went wrong!' );
    })
  );
 }

 getCoronaVirusOverallNews(){
  
  return this._http.get('https://newsapi.org/v2/top-headlines?q=covid&'+
  'pageSize=100&'+
  'apiKey=48a787e36e5d46a1a775fa69ff0e75ab').pipe(
    map((data:news[])=>{
      return data;
    }), catchError( error => {
      return throwError( 'Something went wrong!' );
    })
  );
 }

}
