import { Component, OnInit } from '@angular/core';
import { Chart } from 'chart.js';  
import { GetDataService } from '../get-data.service';
import { StateWise } from '../data';

@Component({
  selector: 'app-table-layout',
  templateUrl: './table-layout.component.html',
  styleUrls: ['./table-layout.component.css']
})
export class TableLayoutComponent implements OnInit {
 
  Linechart ;  
  stateCountry : StateWise[];
  constructor(private _getdata : GetDataService) { }

  ngOnInit(): void {

    this.getStateCalculationForTotalCases();
  }

  getStateCalculationForTotalCases(){
    this._getdata.getCountryWiseStateDataIndia().subscribe((data:any)=>{
      
      this.stateCountry = data.statewise;
    console.log(this.stateCountry.map(element=>element.confirmed));
    console.log(this.stateCountry.map(element=>element.state));

    this.stateCountry.reverse().pop();

    this.stateCountry.reverse();
    
Chart.defaults.global.responsive = true;
    this.Linechart = new Chart('canvas', {  
      type: 'line', 
       
      data: {  
        labels: this.stateCountry.map(element=>element.state),  
        datasets: [  
          {  
            data: this.stateCountry.map(element=>element.confirmed),  
            borderColor: '#e96082',  
            backgroundColor : '#70323f',
            label : 'Total Cases'
          }
            ,
          {  
            data: this.stateCountry.map(element=>element.active),  
            borderColor: '#3cb371',  
            backgroundColor : 'lightblue',
            label : 'Active Cases'
          } 
        ]  
      },  
      
      options: {  
        legend: {  
          display: true  
        }
        ,  
        tooltips: {
          mode: 'nearest'
      },
        scales: {  
          xAxes: [{  
            display: true  
          }],  
          yAxes: [{  
            display: true  
          }],  
        }  
      }  
    });        
    }  
    
    )
    
}

}
