import { Component, ViewChild, Output, OnInit, AfterViewInit } from '@angular/core';
import { ViewportScroller } from '@angular/common';
import { map, count } from 'rxjs/operators';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import {Chart} from 'chart.js'
import { MatTableDataSource } from '@angular/material/table';
import { Data, Router } from '@angular/router';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { EventEmitter } from 'protractor';
import { MatSort } from '@angular/material/sort';
import { GetDataService } from './get-data.service';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { GlobalData, CountryWiseData, StateWise } from './data';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, AfterViewInit  {

  title = 'covidApp';
  public dataJson;
  public globalData : GlobalData;
 public countryWiseData=[];
  refreshDate;
  DateDiff;
  arrayCountries : CountryWiseData[];
  countriesData :CountryWiseData;
  public totalCases:number;
  public activeCases:number;
  public Cured:number;
  public Deaths:number;
 redcolor='#cf6679';
 greenColor='#418786';
 totalColor='#018716';
 isChart = true;
 isData = false;

 selectedValue:string;
 
 continents=['Asia','Africa','Europe','North America','South America','Australia/Oceania','Antarctica']
  tableColumns  :  string[] = ['Name','TotalCases','ActiveCases','Cured','Deaths'];
  moreTablesToLoad = ['India','United States of America'];
  @ViewChild(MatSort) sort: MatSort;

  // @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;  
  
dataSource : MatTableDataSource<CountryWiseData>;

constructor(private breakpointObserver: BreakpointObserver,
  private _scroll : ViewportScroller,private _route:Router,
  private _getdata : GetDataService) {}
 
 ngOnInit() {
  
    this.getCalculation();
     //this.dataSource.paginator = this.paginator;   
    
  }
  
  isTotalVisibile=false;
  isLoading = true;

  ngAfterViewInit(): void {   

   
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if(filterValue.length>0)
    {
      this.isTotalVisibile=true;
    }
    else
    this.isTotalVisibile=false;
   
  }

  scroll(el: string) {
console.log(el);
    this._scroll.scrollToAnchor(el);
    
}

  async getCalculation(){ 

   
    await this._getdata.getJsonData().subscribe((data)=>{
      
      this.dataJson=data;   
  
      this.globalData=this.dataJson.Global;
      
      this.refreshDate=this.dataJson.Date;
      
      this.countryWiseData=this.dataJson.Countries; 
         
      
      this.dataSource = new MatTableDataSource(this.countryWiseData);

      this.dataSource.sort = this.sort
      
      
        this.totalCases = this.countryWiseData.map(t => t.TotalConfirmed).reduce((acc, value) => acc + value, 0);
        this.Cured=this.countryWiseData.map(t => t.TotalRecovered).reduce((acc, value) => acc + value, 0);
          
        this.Deaths=this.countryWiseData.map(t => t.TotalDeaths).reduce((acc, value) => acc + value, 0);  
        
        
       this.activeCases= (this.totalCases) - (this.Cured)          
      });     
   
  }
  
  goToCharts(){

    this.isChart = true;
 this.isData = false;
 
    // this._route.navigate(['/charts']);
    
  }
  goToData(){
    this.isChart = false;
    this.isData = true;
    // this._route.navigate(['/data']);
    
  }
}


