  getNews(){ 
    this._getdata.getCoronaVirusNews().subscribe((data:any)=>{
      this.news=data.articles;
     
      this.filteredNews = this.news.filter((t=>(t.source.name)==="Hindustan Times" || 
      (t.source.name)==="The Times of India"
      )
      );
      

      this.isReady=true;
   
  }
    );
  }

