  ngOnInit(): void {
    this.getCalculation();

    this.getStateCalculationForTotalCases();   
    this.getTimeLine(); 

    this.getCuredTimeLine(); 
    this.getDeathTimeLine();  
    this.getNews();
   
   
 
  }

  getStateCalculationForTotalCases(){
    
    
    this._getdata.getCountryWiseStateDataIndia().subscribe((data:any)=>{
    
      this.stateCountry = data.statewise;

      this.lastRefresh = this.stateCountry[0].lastupdatedtime;
      this.deltaConfirmed = this.stateCountry[0].deltaconfirmed;
      this.cards[0].deltaNumber=this.deltaConfirmed;



      this.deltaRecovered  = this.stateCountry[0].deltarecovered;
      this.cards[2].deltaNumber=this.deltaRecovered;
      
      this.cards[1].deltaNumber= (this.deltaConfirmed - this.deltaRecovered);

      this.deltaDeaths = this.stateCountry[0].deltadeaths;
      this.cards[3].deltaNumber=this.deltaDeaths;
    

      this.refreshdate = this.stateCountry.reverse().pop().lastupdatedtime;

    this.stateCountry.reverse();

    
      
      this.totalCases = this.stateCountry.map(t => Number(t.confirmed)).reduce((acc, value) =>  acc + value, 0);
      this.cards[0].cardNumber=this.totalCases ;
      
      this.Cured=this.stateCountry.map(t => Number(t.recovered)).reduce((acc, value) => acc + value, 0);
      this.cards[2].cardNumber=this.Cured ;
      this.Deaths=this.stateCountry.map(t => Number(t.deaths)).reduce((acc, value) => acc + value, 0);  
      this.cards[3].cardNumber=this.Deaths ;
      
     this.activeCases= (this.totalCases) - (this.Cured)
     this.cards[1].cardNumber=this.activeCases ;
     this.dataready=true;
    

    

    this.chartData=[
      {data : this.stateCountry.map(element=>element.confirmed),label:'Confirmed Cases'},
      {data : this.stateCountry.map(element=>element.active),label:'Active Cases'}
  ];
    
    this.chartLabels = this.stateCountry.map(element=>element.state);
    this.chartReady=true;      
    }  
    
    )
    
}

getTimeLine(){
  this._getdata.getTimeLine().subscribe((data:any)=>{
      
    this.timeLine = data.cases_time_series;
    


  this.chartDataTimeLine=[
    {data : this.timeLine.map(element=>element.totalconfirmed),
      borderColor: "#ffd300",
      pointBorderColor: "#ffd300",
      pointBackgroundColor: "#ffd300",
      pointHoverBackgroundColor: "#ffd300",
      pointHoverBorderColor: "#ffd300",
      //pointBorderWidth: 10,
      pointHoverRadius: 7,
      pointHoverBorderWidth: 2,
      pointRadius: 2,
      fill: false,
      borderWidth: 3
    }    
];
  
  this.chartLabelsTimeLine = this.timeLine.map(element=>element.date);
  this.chartReadyTimeLine=true;

  }
  )
}


getCuredTimeLine(){
  console.log('Cure clicked');
  this._getdata.getTimeLine().subscribe((data:any)=>{
      
    this.timeLine = data.cases_time_series;
  


  this.chartDataCuredTimeLine=[
    {data : this.timeLine.map(element=>element.totalrecovered),
      borderColor: "#80b6f4",
            pointBorderColor: "#80b6f4",
            pointBackgroundColor: "#80b6f4",
            pointHoverBackgroundColor: "#80b6f4",
            pointHoverBorderColor: "#80b6f4",
            //pointBorderWidth: 10,
            pointHoverRadius: 7,
      pointHoverBorderWidth: 2,
      pointRadius: 2,
      fill: false,
      borderWidth: 3
    }    
];
  
  this.chartLabelsCuredTimeLine = this.timeLine.map(element=>element.date);
  this.chartReadyCuredTimeLine=true;

  }
  )
}

getDeathTimeLine(){
  this._getdata.getTimeLine().subscribe((data:any)=>{
      
    this.timeLine = data.cases_time_series;
   


  this.chartDataDeathsTimeLine=[
    {data : this.timeLine.map(element=>element.totaldeceased),
      borderColor: "#c21807",
      pointBorderColor: "#c21807",
      pointBackgroundColor: "#c21807",
      pointHoverBackgroundColor: "#c21807",
      pointHoverBorderColor: "#c21807",
      //pointBorderWidth: 10,
       pointHoverRadius: 7,
      pointHoverBorderWidth: 2,
      pointRadius: 2,
      fill: false,
      borderWidth: 3
    }    
];
  
  this.chartLabelsDeathsTimeLine = this.timeLine.map(element=>element.date);
  this.chartReadyDeathsTimeLine=true;
  if(this.chartReadyDeathsTimeLine === true && this.chartReadyCuredTimeLine === true
    && this.chartReadyTimeLine === true){
      this.isLoader = false;
      this.isData = true;
      
    }
  
  }
  )
}




 getCalculation(){ 
   
   this._getdata.getJsonData().subscribe((data)=>{
    
    this.dataJson=data;   
    this.globalData=this.dataJson.Global;   
    this.countryWiseData=this.dataJson.Countries; 
    this.countryWiseData.sort(function(x,y){
      return y.TotalConfirmed - x.TotalConfirmed
    });
    this.chartDataOverall=[{

      data : this.countryWiseData.map(element=>element.TotalConfirmed),label:'Active Cases'
    }];
    
    this.chartLabelsOverall = this.countryWiseData.map(element=>element.Country);

    this.chartReadyOverall=true;
               
    });     
 
}

getNews(){ 
  this._getdata.getCoronaVirusNews().subscribe((data:any)=>{
    this.news=data.articles;
   this.filteredNews = [];
    this.filteredNews = this.news.filter((t=>(t.source.name)==="Hindustan Times" || 
    (t.source.name)==="The Times of India"
    )
    );
  console.log(this.news);
console.log(this.filteredNews);
    
 
}
  );
}

