import { Component, OnInit } from '@angular/core';
import { GetDataService } from '../get-data.service';
import { StateWise } from '../data';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-india-state-wise',
  templateUrl: './india-state-wise.component.html',
  styleUrls: ['./india-state-wise.component.css']
})
export class IndiaStateWiseComponent implements OnInit {

  tableColumns  :  string[] = ['Name','TotalCases','ActiveCases','Cured','Deaths'];
  dataSource : MatTableDataSource<StateWise>;
  redcolor='#cf6679';
  greenColor='#418786';
  totalColor='#018716';
  isTotalVisibile : boolean;

  totalCases : number;
  activeCases : number;
  Cured : number;
  Deaths : number;

  stateCountry : StateWise[];
  stateCountryTotal : any;

  constructor(private _getdata : GetDataService) { }

  ngOnInit(): void {
    this.getStateCalculation();
  }

  getStateCalculation(){
    this._getdata.getCountryWiseStateDataIndia().subscribe((data:any)=>{
      console.log(data);
      this.stateCountry = data.statewise;

      this.stateCountryTotal = this.stateCountry.reverse().pop();

      this.totalCases = this.stateCountryTotal.confirmed;
      this.activeCases = this.stateCountryTotal.active;
      this.Cured = this.stateCountryTotal.recovered;
      this.Deaths = this.stateCountryTotal.deaths;
      
      this.stateCountry.reverse();
      

      this.dataSource = new MatTableDataSource(this.stateCountry);
      
    })

}

applyFilter(event: Event) {
  const filterValue = (event.target as HTMLInputElement).value;
  this.dataSource.filter = filterValue.trim().toLowerCase();

  if(filterValue.length>0)
  {
    this.isTotalVisibile=true;
  }
  else
  this.isTotalVisibile=false;
 
}
}
