import { Component, OnInit } from '@angular/core';
import { news } from '../data';
import { GetDataService } from '../get-data.service';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.css']
})
export class FilterComponent implements OnInit {
  continents=['Asia','Africa','Europe','North America','South America','Australia/Oceania','Antarctica']
  news:news[];
 
  filteredNews : news[];
  isReady=false;
  constructor(private _getdata : GetDataService) { }

  ngOnInit(): void {
    this.getNews();
  }

  goToContinent(name:string){
    console.log(name);
  }

  getNews(){ 
    this._getdata.getCoronaVirusOverallNews().subscribe((data:any)=>{
      this.news=data.articles;
     
      this.filteredNews = this.news.filter((t=>(t.source.name)==="Hindustan Times" || 
      (t.source.name)==="The Times of India"  || 
      (t.source.name)==="Financial Times" || 
      (t.source.name)==="The Irish Times"  || 
      (t.source.name)==="News24"  || 
      (t.source.name)==="Engadget"  || 
      (t.source.name)==="Globo"  || 
      (t.source.name)==="The Verge"  || 
      (t.source.name)==="Google News"  || 
      (t.source.name)==="Fox News"  || 
      (t.source.name)==="CBC News"
      )
      );
      console.log(this.news);
      console.log(this.filteredNews);

      this.isReady=true;
   
  }
    );
  }

  openLink(url:string){
    window.open(url);
  }

}
